module.exports = "import Alert from 'react-bootstrap/Alert';\n\nfunction BasicExample() {\n  return (\n    <>\n      {[\n        'primary',\n        'secondary',\n        'success',\n        'danger',\n        'warning',\n        'info',\n        'light',\n        'dark',\n      ].map((variant) => (\n        <Alert key={variant} variant={variant}>\n          This is a {variant} alert—check it out!\n        </Alert>\n      ))}\n    </>\n  );\n}\n\nexport default BasicExample;\n"